.App-main {
  background-color: #1d1e20;
  display: flex;
  flex-direction: row;
  width: 1100px;
  height: 600px;
  border-radius: 3px;
  font-family: 'Roboto';
}
.App-nav {
  display: flex;
  flex-direction: column;
}
.Nav-icon-container{
  width: 100px;
  height: 80px;
  display: flex;
  align-items: center;
  text-align: center;
  justify-content: center;
}
.Nav-upload-container{
  background: rgb(7,182,121);
  background: linear-gradient(137deg, rgba(7,182,121,1) 31%, rgba(4,152,98,1) 75%);
  margin-top:0;
  margin-left:0;
  margin-bottom: 50px;
  width: 100px;
  height: 100px;
  display: flex;
  align-items: center;
  text-align: center;
  justify-content: center;
  border-radius: 3px;
}
.Nav-upload {
  color:#1d1e20;
  transform: scale(2.5);
}
.Nav-icon {
  color: #adadad;
  transform: scale(1.7);
}
.Nav-icon-active{
  color: rgb(7,182,121);
  color: linear-gradient(137deg, rgba(7,182,121,1) 31%, rgba(4,152,98,1) 75%);  
  transform: scale(1.7);
}
.Nav-icon-container :hover {
  color: rgb(7,182,121);
  color: linear-gradient(137deg, rgba(7,182,121,1) 31%, rgba(4,152,98,1) 75%);  
  cursor: pointer;
}
.Nav-upload-container :hover{
  cursor: pointer;
}
.App-photo{
  background-color: grey;
  width: 25vw;
  min-width: 430px;
  position: relative;
  top: -5vw;
  height: 750px;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  border-radius: 2px;
}
.App-photo-details{
  position: absolute;
  display: flex;
  font-size: 42px;
  color: white;
  font-weight: bold;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin-left: 15%;
}
.App-photo-details p{
  margin-bottom: 20px;
  font-size: 16px;
  color: white;
  font-weight: bold;
}
.App-details{
  padding: 40px;
  color: #adadad;
  width: 100%;
  max-width: 500px;
  overflow-y: auto;

  text-align: justify;
  line-height: 24px;
  font-size: 15px;
}
.App-details::-webkit-scrollbar {
  -webkit-appearance: none;
}
.App-details::-webkit-scrollbar:vertical {
  width: 5px;
}
.App-details::-webkit-scrollbar-thumb {
  border-radius: 8px;
  background-color: #adadad;
}
.App-details-title{
  font-size: 32px;
  color: white;
  padding-bottom: 20px;
  font-weight: 700;
}
.App-details-title p{
  font-size: 18px;
  color: rgb(7,182,121);
  color: linear-gradient(137deg, rgba(7,182,121,1) 31%, rgba(4,152,98,1) 75%);  
}
.App-details-services p{
  font-size: 32px;
  font-weight: 700;
  color: white;
}
.App-details-services-table{
  display: flex;
  flex-direction: column;
}
.App-details-services-table-row{
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  margin-bottom: 20px;
}
.App-details-services-table-row p {
  color:white;
  font-size: 18px;
  font-weight: 700;
}
.App-details-services-table-row div{
  padding-right: 15px; 
  text-align: left;
}
.Service-icon{
  color: rgb(7,182,121);
  color: linear-gradient(137deg, rgba(7,182,121,1) 31%, rgba(4,152,98,1) 75%);  
  padding-left: 5px;
  transform: scale(1.7);
}
.App-details-facts-row{
  display:flex;
  flex-direction: row;
  justify-content: space-between;
}
.App-details-facts-row p{
  font-size: 30px;
  color: rgb(7,182,121);
  color: linear-gradient(137deg, rgba(7,182,121,1) 31%, rgba(4,152,98,1) 75%);  
  margin-bottom: 15px;
}
.App-details-fact{
  font-weight: 700;
  color: white;
  font-size: 18px;
}
hr{
  margin-top: 30px;
  margin-bottom: 30px;
  background-color: #adadad;
}
.transition-fadein{
  opacity: 1;
  transition: all 250ms linear;
}
.transition-fadeout{
  opacity: 0;
  transition: all 250ms linear;
}
.App-details-contact-column{
  display: flex;
  flex-direction: column;
}
.App-details-contact-icon{
  color: #07b679;
  color: linear-gradient(137deg, rgba(7,182,121,1) 31%, rgba(4,152,98,1) 75%); 
  margin-top: 20px;
  margin-right: 30px;
}
.App-details-contact-text{
  color: white;
  font-weight: 700;
  font-size: 16px;
}
.Contact-form{
  display: flex;
  flex-direction: column;
}
.Work-container{
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}
.Work-column{
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.Work-image{
  width: 230px;
  margin-bottom: 30px; 
  border-radius: 2px;
}
.Work-div{
  position: relative;
  text-align: center;
  border-top: solid 5px;
  border-top-color: rgb(7,182,121);
}
.Work-blank{
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  background-color: black;
  opacity: 0.7;
  border-radius: 2px;
  padding: 5px;
  color: white;
}
@media screen and (max-width: 1140px) {
  .App-photo{
    visibility: hidden;
    width: 0px;
    min-width: 0px;
  }
  .App-main{
    width:680px;
  }
 }
 @media screen and (max-width: 690px) {
   .App-photo{
     visibility: hidden;
     width: 0px;
     min-width: 0px;
   }
   .App-main{
     width:400px;
     overflow: hidden;
   }
   .App-nav{
     width: 50px;
   }
   .Nav-upload-container{
     width: 50px;
     height: 50px;
   }
   .Nav-upload{
     transform: scale(1.4);
   }
   .Nav-icon-container{
     width: 50px;
     height: 80px;
     display: flex;
     align-items: center;
     text-align: center;
     justify-content: center;
   }
   .App-details-facts-row{
     display: flex;
     flex-direction: column;
     text-align: center;
   }
   .Work-container{
     flex-direction: column;
   }
  }
  .Main-photo{
    display: relative;
    width: 430px;
    height: 750px;
  }





  .loader,
.loader:after {
  border-radius: 50%;
  width: 10em;
  height: 10em;
}
.loader {
  margin:15% 5% 15% 10%;
  font-size: 10px;
  position: absolute;
  text-indent: -9999em;
  z-index: 10;
  border-top: 1.1em solid rgba(255, 255, 255, 0.2);
  border-right: 1.1em solid rgba(255, 255, 255, 0.2);
  border-bottom: 1.1em solid rgba(255, 255, 255, 0.2);
  border-left: 1.1em solid #07b679;
  -webkit-transform: translateZ(0);
  -ms-transform: translateZ(0);
  transform: translateZ(0);
  -webkit-animation: load8 1.1s infinite linear;
  animation: load8 1.1s infinite linear;
}
@-webkit-keyframes load8 {
  0% {
    -webkit-transform: rotate(0deg);
    transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
    transform: rotate(360deg);
  }
}
@keyframes load8 {
  0% {
    -webkit-transform: rotate(0deg);
    transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
    transform: rotate(360deg);
  }
}